* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    transition: transform 0.25s;
}

img {
    width: fit-content;
}

ul {
    li {
        list-style: none;
    }
}

a {
    text-decoration: none;
}

button {
    background-color: transparent;
    border: none;
}